import React from 'react'
import CommonLoginBg from 'components/CommonLoginBg/CommonLoginBg';
import { Form, Input, Select, Button, Divider } from 'antd';
import { IVerifyPhone } from './verify.d';
import { useHistory } from "react-router-dom";
const { Option } = Select;

function VerifyPhone() {
    const prefixSelector = (
        <Form.Item name="areacode" noStyle>
            <Select style={{ width: 80 }}>
                <Option value="86">+86</Option>
                <Option value="87">+87</Option>
            </Select>
        </Form.Item>
    );
    let history = useHistory();
console.log(history);
    const verifyForm = (value: IVerifyPhone) => {
        history.push(`/login/${value.areacode}/${value.phone}`)
        // history.push({ path : '/login' ,query : { name: ' sunny'} })
    }

    return (
        <div className='verify-phone'>
            <CommonLoginBg>
                <div className='form-box'>
                    <Form
                        name='verify-phone'
                        onFinish={verifyForm}
                        initialValues={{
                            areacode: '86',
                        }}
                    >
                        <Form.Item
                            name="phone" rules={[{ required: true, message: 'Please input your phone!' }]}>
                            <Input addonBefore={prefixSelector} placeholder='请输入手机号' style={{ width: '100%' }} />
                        </Form.Item>
                        <Form.Item>
                            <Button type='primary' htmlType="submit" style={{ width: '100%' }}>
                                继续
                                </Button>
                        </Form.Item>
                    </Form>
                    <Divider>
                        <span style={{ fontSize: '80%', color: '#ccc' }}>
                            或
                        </span>
                    </Divider>
                    <div className='login-type'>
                        <img src="" alt="" />
                        <span>
                            微信登录
                        </span>
                    </div>
                </div>
            </CommonLoginBg>
        </div>

    )
}

export default VerifyPhone
